import axios from 'axios'
import Cookies from 'js-cookie'
export default {
  name: 'frameList',
  data () {
    return {
      tag: this.Run,
      list: []
    }
  },
  mounted () {
    let level = parseInt(Cookies.get('level'))
    if (level === this.PI) this.getProjectManager()
    else if (level === this.assistant) this.getProjectList()
    else {
      this.getProjectMember()
      this.getProjectManager()
    }
  },
  methods: {
    goFrameDetail (id, name) {
      sessionStorage.setItem('pid', id)
      sessionStorage.setItem('pname', name)
      this.$router.push('/projectFrame')
    },
    getProjectList () {
      axios.get(this.url + '/project/all')
        .then(this.getProjectListSucc)
    },
    getProjectManager () {
      axios.get(this.url + '/project/manager/' + Cookies.get('id'))
        .then(this.getProjectListSucc)
    },
    getProjectMember () {
      // axios.get('/static/testData/module.json')
      axios.get(this.url + '/project/member/' + Cookies.get('id'))
        .then(this.getProjectListSucc)
    },
    getProjectListSucc (res) {
      if (res.status === 200) {
        res = res.data
        let projectList = this.list.concat(res)
        const arr = new Map()
        this.list = projectList.filter((projectList) => !arr.has(projectList.id) && arr.set(projectList.id, 1))
      } else alert(res.data)
    }
  }
}



